:root {
  --lime-green: hsl(163, 72%, 41%);
  --bright-red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--dark-bg);
  color: var(--dark-text1);
  transition: 0.3s;
}
body.light {
  background-color: var(--light-bg);
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  padding: 0.9375rem 1.5625rem;
  width: 100%;
  max-width: 1162px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  z-index: 2;
}

.top-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 15rem;
  background-color: var(--dark-top-bg);
  z-index: 1;
  border-bottom-left-radius: 1.25rem;
  border-bottom-right-radius: 1.25rem;
  transition: 0.3s;
}

body.light .top-background {
  background-color: var(--light-top-bg);
}

.top {
  position: relative;
}

.bottom {
  margin-top: 2.8125rem;
}

.header {
  padding: 1.25rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 960px) {
  .header {
    flex-direction: column;
  }
}
.header__title {
  margin-bottom: 5px !important;
}
@media (max-width: 960px) {
  .header__left {
    width: 100%;
    border-bottom: solid 1px #373A4D;
    padding-bottom: 1.5rem;
  }
}
.header__right {
  display: flex;
  align-items: center;
  margin-bottom: 0.3125rem;
}
@media (max-width: 960px) {
  .header__right {
    justify-content: space-between;
    width: 100%;
    padding-top: 0.9375rem;
    margin-bottom: -0.3125rem;
  }
}
.header__right-toggle {
  position: relative;
  width: 3.125rem;
  height: 1.5rem;
  display: inline-block;
  margin-left: 0.75rem;
  cursor: pointer;
}
.header__right-toggle label {
  position: absolute;
  inset: 0;
  z-index: 10;
  width: 100%;
  height: 1.5rem;
  cursor: pointer;
}
.header__right-input {
  opacity: 0;
  width: 0;
  height: 0;
}
.header__right-input:checked + .header__right-slider {
  background-image: linear-gradient(90deg, var(--toggle) 0%, var(--toggle) 100%);
}
.header__right-input:checked + .header__right-slider:before {
  background-color: var(--dark-text2);
  transform: translateX(1.5625rem);
}
.header__right-slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(90deg, var(--toggle-bg-start) 0%, var(--toggle-bg-end) 100%);
  border-radius: 3.125rem;
  transition: 0.4s;
}
.header__right-slider:before {
  content: " ";
  position: absolute;
  width: 1.125rem;
  height: 1.125rem;
  left: 0.1875rem;
  bottom: 0.1875rem;
  border-radius: 3.125rem;
  background-color: var(--dark-bg);
  transition: 0.4s;
}

.text-1, .text-2, .text-3, .text-4, .text-5, .text-6, .text-7 {
  margin-top: 0;
  margin-bottom: 0;
}

.text-1 {
  font-size: 3.375rem;
  font-weight: 700;
  line-height: 1.95;
  color: var(--dark-text2);
}

body.light .text-1 {
  color: var(--light-text2);
}

.text-2 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--dark-text2);
  line-height: 1;
}

body.light .text-2 {
  color: var(--light-text2);
}

.text-3 {
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--dark-text2);
}
@media (max-width: 960px) {
  .text-3 {
    font-size: 1.5rem;
  }
}

body.light .text-3 {
  color: var(--light-text2);
}

.text-4 {
  font-size: 1.53125rem;
  font-weight: 700;
  color: var(--dark-text2);
}

body.light .text-4 {
  color: var(--light-text2);
}

.text-5 {
  font-size: 0.890625rem;
  font-weight: 700;
  color: var(--dark-text1);
}

body.light .text-5 {
  color: var(--light-text1);
}

.text-6 {
  font-size: 0.765625rem;
  font-weight: 700;
  color: var(--dark-text1);
}

body.light .text-6 {
  color: var(--light-text1);
}

.text-7 {
  font-size: 0.75rem;
  font-weight: 400;
  color: var(--dark-text1);
  letter-spacing: 0.3125rem;
  text-transform: uppercase;
}

body.light .text-7 {
  color: var(--light-text1);
}

.positive-change {
  font-size: 0.75rem;
  color: var(--lime-green);
  font-weight: 700;
}
.positive-change:before {
  content: "";
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid var(--lime-green);
  transform: translateY(-0.1875rem);
  display: inline-block;
  margin-right: 0.3125rem;
}

.negative-change {
  font-size: 0.75rem;
  color: var(--bright-red);
  font-weight: 700;
}
.negative-change:before {
  content: "";
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 4px solid var(--bright-red);
  transform: translateY(-0.125rem);
  display: inline-block;
  margin-right: 0.3125rem;
}

.cards {
  display: grid;
  grid-template-columns: repeat(4, minmax(max-content, 1fr));
  column-gap: 1.75rem;
  row-gap: 1.5rem;
  margin-top: 1.5625rem;
}
@media (max-width: 960px) {
  .cards {
    grid-template-columns: 1fr;
    row-gap: 1.5625rem;
  }
}

.top__card {
  width: 100%;
  height: auto;
  padding: 2rem 1.6875rem 1.4375rem;
  text-align: center;
  position: relative;
  background-color: var(--dark-card);
  border-radius: 0.4375rem;
  overflow: hidden;
  cursor: pointer;
  transition: 0.3s;
}
.top__card:hover, .top__card:active {
  background-color: #333A56;
}
.top__card-facebook:before {
  content: "";
  width: 100%;
  height: 0.25rem;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--facebook);
}
.top__card-twitter:before {
  content: "";
  width: 100%;
  height: 0.25rem;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--twitter);
}
.top__card-instagram:before {
  content: "";
  width: 100%;
  height: 0.25rem;
  position: absolute;
  top: 0;
  left: 0;
  background-image: linear-gradient(90deg, var(--instagram-start) 0%, var(--instagram-end) 100%);
}
.top__card-youtube:before {
  content: "";
  width: 100%;
  height: 0.25rem;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--youtube);
}
.top__card-user {
  display: flex;
  justify-content: center;
  align-items: center;
}
.top__card-logo {
  margin-right: 0.4375rem;
}
.top__card-followers-text {
  margin-top: -1.25rem;
  margin-bottom: 1.5625rem;
}

.bottom__card {
  padding: 1.5625rem 2rem 1.5625rem 1.5625rem;
  background-color: var(--dark-card);
  border-radius: 0.4375rem;
  cursor: pointer;
  transition: 0.3s;
}
.bottom__card:hover, .bottom__card:active {
  background-color: #333A56;
}
.bottom__card-text {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.4375rem;
}
.bottom__card-numbers {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

body.light .top__card {
  background-color: var(--light-card);
}
body.light .top__card:hover, body.light .top__card:active {
  background-color: #E1E3F0;
}
body.light .bottom__card {
  background-color: var(--light-card);
}
body.light .bottom__card:hover, body.light .bottom__card:active {
  background-color: #E1E3F0;
}